

header{

    width: 90vw;
    margin: auto;
    
    
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height:40px;
    padding: 30px 0;

    img{
        height: 30px;
        width: auto;
    }

    p{
        font-family: 'Courier New', Courier, monospace;
    }
    .style-banner{
        
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        font-size: 14px;
        p{
            margin: 0 12px;
        }
    }
    
}
